::-webkit-scrollbar {
  width: 0.4vw;
}
::-webkit-scrollbar-track {
  border-radius: 0px;
  background: rgb(194, 194, 194);
}

::-webkit-scrollbar-thumb {
  background:rgb(8, 32, 50);
  border-radius: 1px;
}

::-webkit-scrollbar-thumb:hover {
  background:linear-gradient(180deg, rgb(8, 32, 50),33%, #F0A500,66%, rgb(8, 32, 50));
}
.dFr {
  display: flex;
}
.dFrc {
  display: flex;
  align-items: center;
}
.dFc {
  display: flex;
  flex-direction: column;
}
body {
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  overflow-x: hidden;
  overflow-y: overlay;
}
.NavigationBar{
  position: fixed;
  height:5vh;
  width:100vw;
  background: rgb(8, 32, 50);
  justify-content: space-between;
  padding-left: 4vw;
  padding-right: 4vw;
  box-sizing: border-box;
  align-items: center;
  z-index: 10;
}
.logoNav{
  margin-top: 0.3vh;
  height: 4.5vh;
}
.ulNav{
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  align-items: center;
  width:22vw;
  justify-content: space-between;
}

.liNav{
  display: flex;
  align-items: center;
  color: white;
  font-size: 0.9vw;
  box-sizing: border-box;
  height:5vh;
  padding-left: 1vw;
  padding-right: 1vw;
  text-decoration: none;

}
.liNav:hover {
  background: rgb(10, 40, 63);
  cursor: pointer;
}

.underNavBar {
  position: fixed;
  top:5vh;
  height:0.5vh;
  width:100vw;
  background: linear-gradient(90deg,rgb(0, 0, 0),10%, #F0A500);
  z-index: 10;
}
.body{
  display: flex;
  flex-direction: row;
  position: relative;
  top:5.5vh;
  height: 94.5vh;
  width:100vw;
}
.bodyLeft{
  min-width:50vw;
  align-items: center;
  box-sizing: border-box;
  padding-left: 4.1vw;
}
.bodyLeftFlex{
  display: flex;
  flex-direction: column;
  gap:1vw;
}
.bodyLeftTitle{
  font-size: 3vw;
  color: rgb(8, 32, 50);
  font-weight:bolder;
  margin-bottom: 3vh;
  opacity: 0;
  animation:bodyLeftTitle;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}

.bodyLetter{
  font-size: 3vw;
  font-weight:bolder;
  line-height: 1;
  color: rgb(8, 32, 50);
}
.bodyLeftText{
  font-size: 2vw;
  color: #F0A500;
  align-items: flex-end;
  letter-spacing: 0.2vw;
}
.CRUDColumn {
  display: flex;
  flex-direction: column;
  gap:1.35vw;
  opacity: 0;
  height: 16vw;
  overflow: hidden;
  animation:CRUDColumn steps(60);
  animation-duration: 3s;
  animation-delay: 2.4s;
  animation-fill-mode: forwards;
}
.bodyLeftDescription{
  width:23vw;
  margin-right: 2vw;
  opacity: 0;
  color: rgb(73, 73, 73);
  line-height: 1.5;
  font-size: 1.07vw;
  animation:bodyLeftDescription;
  animation-duration: 1s;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}
.bodyRight{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width:50vw;
  background: rgb(8, 32, 50);
  border-radius: 180% 0% 0% 180%;
  animation:bodyRight;
  animation-duration: 1s;
}
.bodyTitleRight{
  color: #F0A500;
  font-size: 3.7vw;
  font-weight:bolder;
  letter-spacing: 0.5vw;
}
.bodySubTitleRight{
  color: white;
  font-size: 2vw;
  margin-top: 3vh;

}
/* ......................................................................................Items */
.DATABASE{
  position: relative;
  top:5vh;
  display: flex;
  flex-direction: row;
}
.items, .itemsCrud{
  display: flex;
  flex-direction: column;
  gap:6vh;
  width: 45vw;
  padding:4vw;
}
.itemsCrud {
  animation: itemsEntry;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: backwards;
}
.item{
  position: relative;
  display: flex;
  flex-direction: row;
  border:0.2vw solid rgb(8, 32, 50);
  padding:1vw;
  border-radius: 0.2vw;
  align-items: center;
  font-size: 2vw;
}
@keyframes itemsEntry {
  from {margin-left:-100vw}
}
.circle {
  display: flex;
  height:12vw;
  width:12vw;
  border-radius: 50%;
  color:#F0A500;
  background: rgb(8, 32, 50);
  align-items: center;
  justify-content: center;
  font-size: 7vw;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin-right:2.2vw;
}
.itemTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:100%;
  font-size: 2.5vw;
  margin-bottom: 1.5vw;
  font-weight: 600;
  font-family: 'Times New Roman', Times, serif;
}
.remEdit {
  display: flex;
}
.rembtn {
  display: flex;
  align-items: center;
  justify-content: center;
  height:1.7vw;
  width:4vw;
  background: rgb(8, 32, 50);
  font-size:0.7vw;
  border-radius: 0.2vw;
  border:none;
  color:#F0A500;
  transform: scale(0);
  margin-left: 1vw;
  font-family: Arial, Helvetica, sans-serif;
  animation: rembtn;
  animation-duration:1s;
  animation-fill-mode: forwards;
}
@keyframes rembtn {to {transform: scale(1);}}
.rembtn:hover {
  cursor: pointer;
  background: rgb(10, 40, 63);
}
.itemDescription{
  display: flex;
  justify-content: space-between;
  gap:2vw;
  width: 100%;
  align-items: flex-start;
}
.oneDescriptoion {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-items:flex-start;
  margin-top: 0.1vw;
  margin-bottom: 0.1vw;
}
.dataDesc {
  font-size: 0.9vw;
  min-width: max-content;
  font-weight: bold;
}
.dataElem {
  font-size: 0.9vw;
  margin-left: 0.5vw;
  color: rgb(44, 44, 44);
}
.staticWord {
  position: fixed;
  top: 40.5vh;
  right: 9vw;
  font-size: 6vw;
  width:29.5vw;
  font-weight: bold;
  color:rgb(8, 32, 50);
  line-height: 4vw;
}
.staticWord2 {
  font-size: 2.4vw;
  letter-spacing: 0.2vw;
  color:#F0A500;
}

.buttonCrud{
  height:2vw;
  width:3vw;
  background:rgb(8, 32, 50) ;
  color:#F0A500;
  text-decoration: none;
  font-size: 1.5vw;
  padding: 0.4vw 0.8vw 0.4vw 0.8vw;
  letter-spacing: 0.2vw;
  border-radius: 0.3vw;
}
.buttonCrud:hover {
  background: rgb(10, 40, 63);
}
.DatAnim{
  animation: DatAnim;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
.backAnim{
  animation: backAnim;
  animation-duration: 1.2s;
  animation-fill-mode: forwards;
}
.ButAnim {
  animation: ButAnim;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-delay: 0.4s;
}
.backButAnim {
  animation: backButAnim;
  animation-duration: 0.5s;
}
@keyframes DatAnim {to { top:14vh;}}
@keyframes ButAnim {99% {position: relative; margin-left:23vw; opacity: 0;} 100% {position: relative; margin-left:23vw; opacity: 0; visibility: hidden;}}
@keyframes form {to {transform:scale(1);opacity: 1;}}

@keyframes backAnim {0% { top:14vh;} 30% { top:14vh;}}
@keyframes backButAnim { 0% {position: relative; margin-left:23vw; opacity: 0;  }  to {margin-left:0vw;}}

.form {
  display: flex;
  flex-direction: column;
  transform:scale(0);
  opacity:0;
  width: 100%;
  font-size: 0.9vw;
  line-height: 1;
  animation: form;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 0.4s;
  height: 53vh;
  color:black;
  border: 0.15vw solid rgb(10, 40, 63);
}
.bigPop {
  position: fixed;
  top: 42vh;
  right: 9vw;
  display: flex;
  flex-direction: column;
  background:white;
  height:53vh;
  width:29vw;
  font-size: 0.9vw;
  line-height: 1;
  border: 0.15vw solid rgb(10, 40, 63);
  animation: popEdit;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  flex-wrap: wrap;
}
@keyframes popEdit {
  to {right: 4vw; top:6.5vw; width:92vw; height: 80vh;}  
}
form {
  display: flex;
  flex-direction: column;
  padding:2vw;
  box-sizing: border-box;
  margin: 0;
  height: 100%;
  width:100%;
  overflow-y:scroll;
}
form > .modify >label{
  font-size: 1.6vw;
  color:black;
  line-height: 1;
  margin-bottom: 0.2vw;
}
form > .modify{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
form > input {
  height:2vw;
  border:none;
  border-bottom: 0.15vw solid rgb(10, 40, 63);
  font-size: 1vw;
  margin-bottom: 1.5vw;
}
.afterInput{
  background: rgba(0, 0, 0, 0.185);
  color:rgb(0, 0, 0);
}
.afterInput:focus {
  outline: none;
}
.submit{
  height:3vw;
  width:5vw;
  background:rgb(8, 32, 50) ;
  color:#F0A500;
  border:none;
  font-size: 1.5vw;
  padding: 0.4vw 0.8vw 0.4vw 0.8vw;
  letter-spacing: 0.2vw;
  border-radius: 0.3vw;
}
.submit:hover, .thxBtn:hover {
  cursor: pointer;
  background: rgb(10, 40, 63);
}
.radio {
  margin-top: 0.7vw;
  margin-bottom: 0.7vw;
  cursor: pointer;
  margin-right: 0.7vw ;
  height: 1vw;
  width: 1vw;
}
.error {
  color: #F0A500;
  font-size: 0.7vw;
  line-height: 1.4;
}
.popUpThank{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  position: absolute;
  height:100%;
  width:100%;
  background: rgba(0, 0, 0, 0.9);
  color: #F0A500;
  font-size: 3vw;
  line-height: 1.4;
  animation: thx;
  transform: scale(1);
  animation-duration: 1s;
}
@keyframes thx {from {transform:scale(0)};to {transform:scale(0)}}
.thxBtn {
  margin-top: 2vh;
  height:4vh;
  width:5vw;
  font-size: 0.8vw;
  letter-spacing: 0.1vw;
  outline: 0.1vw solid #F0A500;
  border:none;
  background:rgb(8, 32, 50) ;
  color: #F0A500;
  font-weight: 600;
  border-radius: 0.2vw;
}
.confirmation {
  color: rgb(8, 32, 50);
  font-size: 0.7vw;
  line-height: 1.4;
}
.editRemove{
  display: flex;
  flex-direction: row;
}
.logoAbout{
  position: fixed;
  right:0;
  width:48vw;
}
.about {
  display: flex;
  flex-direction: row;
  position: relative;
  padding-top:5vh;
  padding-bottom:5vh;
  align-items: center;
  background:rgb(8, 32, 50);
  min-height: 95vh;

}
.textWritten{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 40%;
  margin-left: 4vw;
  margin-right: 4vw;
}
.aboutTitle {
  position: relative;
  font-size: 3vw;
  margin-top: 4.5vw;
  font-weight: bold;
  color: #F0A500;
  letter-spacing: 0.13vw;

}
.aboutDescription {
  position: relative;
  margin-top: 1.2vw;
  font-size: 1.2vw;
  color: rgb(192, 192, 192);
  letter-spacing: 0.02vw;
  line-height: 1.5;
}
/* ......................................................................................Animations */
@keyframes bodyRight {
  0% {margin-left: 100vw;}
  100% { margin-left: 0vw;} 
}
@keyframes bodyLeftTitle {
from {opacity: 0;}
 to {opacity: 1;}
}
@keyframes bodyLeftDescription {
  from {opacity: 0;}
   to {opacity: 1;}
  }
@keyframes CRUDColumn {
  0%{height: 0px;opacity: 1;}
  25%{height: 0vw;}
  25.5%{height: 4vw;}
  50%{height: 4vw;}
  50.5%{height: 8.1vw;}
  75%{height: 8.1vw;}
  75.5%{height: 12vw;}
  99.9%{height:12vw;opacity:1;}
  100%{height:16vw;opacity:1;}
  }




  .smallScreen{display: none;}

